					</view>
					<view class="xuanze_body">
						<view class="xuanze_item hei_30" :class="xuanze == index ? 'xuanzhong' : ''" @click="xuan(index, item.lawyervipid)" v-for="(item, index) in lawyervip">
							<view class="xuanze_item_top">
