					</view>

					<view class="fangshi">
						<!-- <view class="fangshi_list_pay" @click="radio(1)">
	            		<view class="fangshi_left hei_28">
	            			<image src="@/static/img/pay_weixin.png" mode="" style="width:36rpx ;height: 31rpx;"></image>
	            			微信支付
	            			<text class="tuijian hong_22">推荐</text>
	            		</view>
	            		<label class="radio"><radio value="1" :checked="zhifu == 1" /></label>
	            	</view> -->
						<view class="fangshi_list_pay" @click="radio(2)">
							<view class="fangshi_left hei_28">
								<image src="@/static/img/pay_zhifubao.png" mode="" style="width: 39rpx;height: 39rpx;"></image>
								支付宝支付
								<text class="tuijian_txt hong_22">推荐</text>
							</view>
							<label class="radio"><radio value="2" :checked="zhifu == 2" /></label>
						</view>
						<view class="fangshi_list_pay" @click="radio(3)">
							<view class="fangshi_left hei_28">
								<image src="@/static/img/yue_pay.png" mode="" style="width:36rpx ;height: 31rpx;"></image>
								余额支付
							</view>
							<label class="radio"><radio value="3" :checked="zhifu == 3" /></label>
						</view>
					</view>
					<view class="kaitong_btn_wai"><button type="" class="kaitong_btn bai_30" @click="save">立即开通</button></view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
export default {
	data() {
		return {
			img_url: uni.getStorageSync('img_url'),
			user: '',
			zhifu: 2,
			xuanze: 0,
			money: '1800',
			lawyervip: '',
			lawyervipid: '',
			is_click: true,
			apppaytype:''
		};
	},
	created() {},
	onLoad(option) {
		this.$http
			.post({
				url: '/mlawyerapi/user/lawyervip'
			})
			.then(res => {
				this.lawyervip = res.data.lawyervip;
				this.lawyervipid = res.data.lawyervip[0].lawyervipid;
			});
		// 获取用户信息
		this.$http
			.post({
				url: '/mlawyerapi/user/getlawyer'
			})
			.then(res => {
				this.user = res.data.user;
			});
			
	
			this.$http
				.post({
					url: '/mapi/index/getapppaytype'
				})
				.then(res => {
					if (res.code == 0) {
						
						this.apppaytype=res.data.zhan.apppaytype
					}
				});
		
	},
	methods: {
		navigateBack() {
			uni.navigateBack();
		},
		radio(i) {
			this.zhifu = i;
		},
		xuan(i, lawyervipid) {
			this.xuanze = i;
			this.lawyervipid = lawyervipid;
		},
		save() {
			if (this.zhifu == 2) {
				
				if(this.apppaytype==1){
					this.h5pay();
					
				}else{
				
				// #ifdef H5
				this.h5pay();
				// #endif

				// #ifdef APP-PLUS
				this.zhifubao_pay();
				// #endif
			}	
				
				
			} else {
				this.yue_pay();
			}
		},
		h5pay() {
			var that = this;
			if (this.is_click) {
				that.is_click = false;
				setTimeout(function() {
					that.is_click = true;
				}, 3000);

				this.$http
					.post({
						url: '/mlawyerapi/user/lawyerviph5',
						data: {
							lawyervipid: that.lawyervipid
						}
					})
					.then(res => {
						if (res.code == 0) {
							
							// #ifdef H5
							that.$http.jspost(res.data.response)
							// #endif
							// #ifdef APP-PLUS
								plus.runtime.openURL('' + res.data.response);
							// #endif
							
						}
					});
			} else {
				uni.showToast({
					title: '稍后操作',
					duration: 2000,
					icon: 'none'
				});
			}
		},
		zhifubao_pay() {
			this.$http
				.post({
					url: '/mlawyerapi/user/lawyervipapp',
					data: {
						lawyervipid: this.lawyervipid
					}
				})
				.then(res => {
					var that = this;
					if (res.code == 0) {
						uni.requestPayment({
							provider: 'alipay',
							orderInfo: res.data.response,
							success: function(res) {
								console.log('success:' + JSON.stringify(res));
								uni.showToast({
									title: '支付成功',
									duration: 2000
								});
							},
							fail: function(err) {
								uni.showToast({
									title: '支付失败',
									duration: 2000,
									icon: 'none'
								});
								console.log('fail:' + JSON.stringify(err));
							}
						});
					}
					console.log(res);
				});
		},
		yue_pay() {
			this.$http
				.post({
					url: '/mlawyerapi/user/accountvip',
					data: {
						lawyervipid: this.lawyervipid
					}
				})
				.then(res => {
					if (res.code == 0) {
						uni.showToast({
							title: '' + res.message,
							duration: 2000,
							icon: 'none'
						});
					}
				});
		}
	},
	filters: {
		timeStamp: function(value) {
			if (value == null) {
				return 'null';
			}
			var i = (value + '').length;
			while (i++ < 13) value = value + '0';
			value = Number(value);
			var date = new Date(value);
			//date.setTime(value);
			var month = date.getMonth() + 1;
			var hours = date.getHours();
			if (hours < 10) hours = '0' + hours;
			var minutes = date.getMinutes();
			if (minutes < 10) minutes = '0' + minutes;
			var time = date.getFullYear() + '-' + month + '-' + date.getDate() + ' ' + hours + ':' + minutes;
			return time;
		}
	}
};
</script>

